<style scoped>
@import "../assets/css/form_contact.css";
</style>
<template>
    <div>
        <v-img src="../assets/images/contact/pattern-yellow-points1.png" class="rect_point"
            max-width="420" max-height="900" contain></v-img>
            <v-img src="../assets/images/rect-yellow.png" max-width="325" max-height="119" class="rect_yellow">
                <div class="text_rect_yellow">
                  <span>{{getForm[0].title}}</span>
            </div>
        </v-img>
        <v-container>
            <v-row>
            <v-col cols="6">
              
               <form class="contact_form">
                <v-text-field
                  v-model="state.name"
                  :error-messages="v$.name.$errors.map(e => e.$message)"
                  label="Name"
                  required
                  outlined
                  color="#C2C2BD"
                  @input="v$.name.$touch"
                 ></v-text-field>
                <v-text-field
                  v-model="state.subject"
                  :error-messages="v$.subject.$errors.map(e => e.$message)"
                  label="Subject"
                  required
                  @input="v$.subject.$touch"
                  outlined
                   color="#C2C2BD"
                ></v-text-field>
                <v-text-field
                  v-model="state.phonenumber"
                  :error-messages="v$.phonenumber.$errors.map(e => e.$message)"
                  label="Phone Number"
                  required
                  color="#C2C2BD"
                  @input="v$.phonenumber.$touch"
                  outlined
                ></v-text-field>
                <v-text-field
                  v-model="state.email"
                  :error-messages="v$.email.$errors.map(e => e.$message)"
                  label="Email Address"
                  required
                  color="#C2C2BD"
                  @input="v$.email.$touch"
                  outlined
                ></v-text-field>
                <v-textarea label="Message..." 
                   v-model="state.message"
                  :error-messages="v$.message.$errors.map(e => e.$message)"
                   required
                   outlined
                   color="#C2C2BD"
                  @input="v$.message.$touch"></v-textarea>
                <v-btn
                  class="me-4 btn_send"
                  @click="v$.$validate"
                  outlined
                 
                >
                  send
                </v-btn>
                  </form>     
             </v-col>
             <ContactInfo></ContactInfo>
             </v-row>
           
          </v-container>
          </div>
      </template>
<script>
import { reactive } from '@vue/composition-api'
import { useVuelidate } from '@vuelidate/core'
import { email, required } from '@vuelidate/validators'
import { mapGetters, mapActions } from "vuex";
import ContactInfo from '@/components/ContactInfo.vue';
export default {
   name:"ContactForm",
   components:{
     ContactInfo
   },
  setup () {
    const initialState = {
      name: '',
      email: '',
     
    }

    const state = reactive({
      ...initialState,
    })

   

    const rules = {
      name: { required },
      subject: { required },
      phonenumber:{ required },
      email: { required, email },
      message:{ required },
    }

    const v$ = useVuelidate(rules, state)

    function clear () {
      v$.value.$reset()

      for (const [key, value] of Object.entries(initialState)) {
        state[key] = value
      }
    }

    return { state,  clear, v$ }
  },
  methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getForm"]),
  },
  mounted() {
   let form={
    topic_type_id:50,
    cat:"form"
   }
   this.fetchTopics(form);
  
  },
}
</script>